//<ПЕРЕМЕННЫЕ>=============================================================
$minWidth: 320px;
$maxWidth: 1920;
$maxWidthContainer: 1170;
$md1: $maxWidthContainer + 12;
$md2: 991.98;
$md3: 767.98;
$md4: 479.98;

$blue: #0439ab;
$yellow: #ffc000;
$dark-text: #001029;

//</ПЕРЕМЕННЫЕ>============================================================

//<ОБНУЛЕНИЕ, ОБЩИЕ ПАРАМЕТРЫ>=============================================
@import "help/null";

body {
	color: $dark-text;
	font-size: 16px;
	font-style: normal;
	font-weight: 400;
	&._lock {
		overflow: hidden;
	}
}
//</ОБНУЛЕНИЕ, ОБЩИЕ ПАРАМЕТРЫ>============================================

//<ОБОЛОЧКА>================================================================
.wrapper {
	position: relative;
	width: 100%;
	min-height: 100%;
	overflow: hidden;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}
//</ОБОЛОЧКА>===============================================================

//</ОСНОВНАЯ СЕТКА>=========================================================
._container {
	max-width: $maxWidthContainer + px;
	padding: 0 15px;
	margin: 0 auto;
	&_hd {
		padding: 0 15px;
		max-width: 1920px;
		margin: 0 auto;
	}
	&_single{
		padding: 0 15px;
		max-width: 800px;
		margin: 0 auto;
	}
}
@media (min-width: 481px) {
	._container_hd {
		padding: 0;
	}
}
//</ОСНОВНАЯ СЕТКА>=========================================================
h2, h3, h4, h5 {
	font-weight: 700;
	line-height: 36px;
}

h2{
	font-size: 32px;
}

h3{
	font-size: 28px;
}
h4, h5{
	font-size: 26px;
}
.title {
	display: inline-block;
	position: relative;
	padding: 20px 0;
	color: $dark-text;
	text-align: right;
	&__span {
		padding-left: 120px;
		position: absolute;
		right: 0;
		top: 0;
		font-weight: 500;
		font-size: 15px;
		line-height: 18px;
		text-transform: uppercase;
		&::before {
			position: absolute;
			content: "";
			width: 100px;
			height: 3px;
			background-color: $yellow;
			left: 0;
			top: 50%;
			transform: translateY(-50%);
		}
	}
	&__title {
		font-size: 32px;
		font-weight: 800;
		line-height: 42px;
	}
	&_wite{
		color: #fff;
	}
}

@media (min-width: 641px) {
	.title {
		&__span {
			font-size: 15px;
		}
		&__title {
			font-size: 48px;
			line-height: 50px;
		}
	}
}


