.intro {
	padding: 80px 0;
	background-color: $blue;
	&__text {
		background-color: $blue;
		color: #fff;
		& span {
			position: relative;
			padding-left: 110px;
			font-size: 15px;
			font-weight: 500;
			text-transform: uppercase;
			&::before {
				content: "";
				position: absolute;
				z-index: 1;
				height: 3px;
				width: 100px;
				left: 0;
				top: 50%;
				transform: translateY(-50%);
				background-color: $yellow;
				border-radius: 3px;
			}
		}
		& h1 {
			margin-top: 40px;
			font-size: 43px;
			font-weight: 600;
			letter-spacing: 0.9px;
		}
	}

	&__img {
		display: none;
	}
}

@media (min-width: 481px) {
	.intro {
		background-color: transparent;
		padding: 0;
		&__wrapp {
			position: relative;
		}
		&__text {
			height: 80%;
			max-width: 450px;
			position: absolute;
			z-index: 5;
			top: 50%;
			transform: translateY(-50%);
			padding: 80px 80px 80px 20px;
			clip-path: polygon(0% 0%, 99.86% 0%, 80.11% 100%, 0% 100%);
			& h1 {
				max-width: 440px;
			}
		}
		&__img {
			margin-left: auto;
			display: block;
			position: relative;
			height: 500px;
			width: 80%;
			clip-path: polygon(17% 0%, 100% 0%, 100% 100%, 0% 100%);
			overflow: hidden;
			& img {
				position: absolute;
				right: 0;
				left: 0;
				width: 150%;
				height: 100%;
				object-fit: cover;
			}
		}
	}
}

@media (min-width: 961px) {
	.intro {
		&__text {
			max-width: 600px;
			width: 100%;
		}
		&__img {
			height: 600px;
		}
	}
}

@media (min-width: 1281px) {
	.intro {
		&__text {
			max-width: 900px;
			width: 100%;
		}
	}
}

