// .service.service.service.service.service.service.service.service.service.service.service.service
.services {
	&__wrap {
		padding: 100px 0;
		text-align: center;
	}
	&__list {
		padding: 25px 0;
		display: flex;
		flex-direction: column;
		text-align: left;
	}
}

.service {
	border: solid 1px #e3e4e5;
	border-radius: 6px;
	margin: 20px 0;
	padding: 50px 50px 40px 50px;
	color: $dark-text;
	font-size: 15px;
	font-weight: 600;
	transition: all 0.3s ease-in-out;
	background-position: 1160px;
	background-repeat: no-repeat;
	background-size: cover;
	&__img {
		position: relative;
		margin-bottom: 25px;
		width: 60px;
		height: 60px;
		& img {
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
		&::before {
			position: absolute;
			content: "";
			right: -30px;
			top: -25px;
			width: 75px;
			height: 75px;
			border-radius: 50%;
			background-color: #efefef;
		}
	}
	&__title {
		margin-bottom: 15px;
		font-size: 20px;
	}

	&__price {
		color: #7e7f80;
		margin-bottom: 80px;
	}
	&__link {
		position: relative;
		color: $dark-text;
		&:after {
			content: "";
			position: absolute;
			right: -50px;
			top: 50%;
			width: 40px;
			height: 40px;
			transform: translateY(-50%);
			background-color: $yellow;
			border-radius: 5px;
		}
		&::before {
			content: "";
			clip-path: polygon(40% 30%, 40% 70%, 60% 50%);
			position: absolute;
			z-index: 10;
			right: -50px;
			top: 50%;
			width: 40px;
			height: 40px;
			transform: translateY(-50%);
			background-color: $dark-text;
		}
	}
}

.service:hover {
	background-position: center;
	background-repeat: no-repeat;
	background-size: cover;
	color: #fff;
}
.service:hover .service__price,
.service:hover .service__link {
	transition: all 0.3s ease-in-out;
	color: #fff;
}

@media (min-width: 641px) {
	.services {
		&__list {
			justify-content: center;
			flex-direction: row;
			flex-wrap: wrap;
			margin: 0 -10px;
		}
	}
	.service {
		flex: 1 1 360px;
		margin: 10px;
	}
}

@media (min-width: 1281px) {
	.service {
		flex: 0 1 360px;
	}
}

