// .work.work.work.work.work.work.work.work.work.work.work.work.work.work.work.work.work.work.work
.work {
	position: relative;
	&::before {
		position: absolute;
		z-index: 1;
		content: "";
		height: 150px;
		width: 100%;
		clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
		left: 0;
		top: 0;
		background-color: #e3e4e5;
	}

	&__number {
		padding: 40px 0;
		z-index: 5;
		position: relative;
	}
}

.number {
	display: flex;
	justify-content: space-between;
	&__item {
		flex: 1 0 140px;
		padding: 10px;
	}

	&__value {
		display: inline-block;
		position: relative;
		font-weight: 700;
		font-size: 30px;
		color: $blue;
		&_plus {
			&::after {
				position: absolute;
				content: "";
				width: 20px;
				height: 20px;
				right: -15px;
				top: -15px;
				background-image: url(../img/icons/plus.svg);
				background-size: contain;
				background-repeat: no-repeat;
			}
		}
		&_precent {
			&::after {
				position: absolute;
				z-index: 10;
				content: "";
				width: 20px;
				height: 20px;
				right: -15px;
				top: -15px;
				background-image: url(../img/icons/precent.svg);
				background-size: contain;
				background-repeat: no-repeat;
			}
		}
	}
	&__descr {
		font-weight: 600;
		font-size: 15px;
		line-height: 22px;
	}
}
@media (min-width: 641px) {
	.work {
		&::before {
			clip-path: polygon(0% 0%, 88% 0%, 82% 100%, 0% 100%);
		}
	}
	.number {
		justify-content: start;

		&__item {
			flex: 0 1 180px;
			padding: 10px;
		}
	}
}

@media (min-width: 961px) {
	.work {
		&::before {
			height: 180px;
		}
	}
	.number {
		justify-content: start;
		&__item {
			flex: 0 1 250px;
			padding: 10px;
		}
		&__value {
			font-size: 60px;
		}
	}
}

.work-stage {
	margin-top: -50px;
	padding: 100px 0;
	background: linear-gradient(rgba(4, 57, 171, 0.85)), url(../img/bg-work.png);
	background-position: center;
	&__wrap {
		text-align: center;
	}
	&__list {
		margin-top: 50px;
		display: flex;
		flex-wrap: wrap;
	}
	&__item {
		flex: 1 1 100%;
		color: #ffffff;
		margin: 40px auto;
		padding: 20px;
		align-items: center;
		z-index: 3;
	}

	&__img {
		position: relative;
		margin: 0 auto;
		width: 180px;
		height: 180px;
		border-radius: 50%;
		background-color: #fff;
		& img {
			position: absolute;
			width: 60%;
			height: 60%;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
			border-bottom: solid 3px $yellow;
			object-fit: contain;
		}
	}
	&__title {
		font-weight: 700;
		font-size: 26px;
		line-height: 31px;
		margin: 15px 0;
	}
	&__descr {
		font-weight: 400;
		font-size: 15px;
		line-height: 24px;
	}
}

@media (min-width: 641px) {
	.work-stage {
		&__item {
			flex: 0 0 50%;
		}
	}
}

@media (min-width: 1025px) {
	.work-stage {
		position: relative;
		&::before {
			content: "";
			position: absolute;
			z-index: 2;
			left: 0;
			bottom: 320px;
			width: 100%;
			height: 200px;
			background-image: url(../img/line.svg);
			background-size: cover;
			background-repeat: no-repeat;
			background-position: center center;
		}
		&__item {
			flex: 0 0 25%;
			&_move {
				padding-top: 50px;
			}
		}
	}
}
