// .advantages.advantages.advantages.advantages.advantages.advantages.advantages.advantages

.advantages {
	padding: 110px 0;
	&__wrap {
		display: flex;
		flex-direction: column;
	}
	&__descr {
		text-align: center;
	}
	&__paragraph {
		font-size: 15px;
		line-height: 25px;
		text-align: justify;
		padding-bottom: 30px;
	}
	&__items {
		text-align: left;
	}
	&__img-wrap {
		position: relative;
	}
	&__img {
		margin-top: 50px;
		position: relative;
		width: 100%;
		padding-bottom: 100%;
		overflow: hidden;
		& img {
			position: absolute;
			width: 100%;
			height: auto;
			top: 0;
			left: 0;
		}
	}
	&__number {
		color: $blue;
		padding: 10px;
		position: absolute;
		width: 100px;
		height: 100px;
		bottom: -30px;
		right: 0;
		background-color: #fff;
		border-left: solid 2px $yellow;
		font-size: 30px;
		font-weight: 800;
		text-transform: uppercase;
		display: block;
		& span {
			margin-top: 10px;
			display: block;
			font-size: 13px;
			font-weight: 600;
			color: $dark-text;
			text-transform: none;
		}
		&::after {
			position: absolute;
			content: "";
			width: 20px;
			height: 20px;
			right: 0px;
			top: 2px;
			background-image: url(../img/icons/plus.svg);
			background-size: contain;
			background-repeat: no-repeat;
		}
	}
}

.advantag {
	display: flex;
	flex-wrap: wrap;
	margin: 0 -10px;
	margin-bottom: 20px;
	&__item {
		padding: 10px;
		display: flex;
	}
	&__icon {
		position: relative;
		flex: 0 0 60px;
		width: 60px;
		height: 60px;
		& img {
			position: absolute;
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
		}
	}
	&__title {
		flex: 0 1 180px;
		margin-left: 8px;
		font-weight: 500;
		font-size: 16px;
		line-height: 25px;
	}
}

.director {
	border-top: solid 1px #e3e4e5;
	padding-top: 20px;
	display: flex;
	align-items: center;
	&__img {
		position: relative;
		width: 90px;
		height: 90px;
		border-radius: 50%;
		overflow: hidden;
		& img {
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	&__data {
		margin-left: 30px;
		display: block;
		font-weight: 500;
		font-size: 18px;
		line-height: 25px;
		text-transform: uppercase;
		& span {
			display: block;
			font-size: 15px;
			line-height: 25px;
			color: #7e7f80;
			text-transform: none;
		}
	}
}

@media (min-width: 1025px) {
	.advantages {
		&__wrap {
			flex-direction: row;
		}
		&__descr {
			padding-right: 40px;
		}

		&__img-wrap {
			align-items: stretch;
			flex: 0 0 35%;
		}
		&__img {
			height: 100%;
		}
	}
}

@media (min-width: 1025px) {
	.advantag {
		flex-wrap: nowrap;
	}
}
