// .feedback.feedback.feedback.feedback.feedback.feedback.feedback.feedback.feedback.feedback

.feedback {
	position: relative;
	background-color: $blue;

	&__wrap {
		background-color: #e3e4e5;
		padding: 40px 0;
	}

	&__contacts {
		display: flex;
		flex-direction: column;
		align-items: center;
	}
	&__form {
		background-color: $yellow;
		padding: 50px 0;
	}
}

.title__descr {
	font-weight: 500;
	font-size: 11px;
	line-height: 16px;
}

.contacts-form {
	&__link {
		position: relative;
		font-weight: 600;
		font-size: 20px;
		line-height: 25px;
		padding-left: 60px;
		margin-top: 40px;
		&::before {
			position: absolute;
			content: "";
			width: 40px;
			height: 40px;
			left: 0;
			top: 50%;
			transform: translateY(-50%);
			background-image: url(../img/icons/phone.svg);
			background-repeat: no-repeat;
			background-position: center;
			background-size: 40%;
			background-color: #fff;
			border-radius: 50%;
		}
	}
	&__mode {
		position: absolute;
		left: 62px;
		top: -15px;
		font-weight: 500;
		font-size: 13px;
		line-height: 13px;
		color: #001029;
	}
}

.form {
	display: flex;
	flex-direction: column;
	padding: 80px 20px;
	&__select {
		margin: 0px auto 20px;
		padding: 16px 30px 16px 30px;
		border-radius: 3px;
		max-width: 500px;
		width: 100%;
	}
	&__label {
		margin: auto;
		margin-bottom: 20px;
		max-width: 500px;
		width: 100%;
	}
	&__input {
		width: 100%;
		padding: 16px 30px 16px 30px;
		border-radius: 3px;
	}
	&__button {
		margin: auto;
		border-radius: 3px;
		padding: 16px 30px 16px 30px;
		max-width: 200px;
		width: 100%;
		background-color: #001029;
		color: #fff;
		transition: background-color 0.3s ease-in-out;
		font-weight: 600;
		font-size: 17px;
		line-height: 21px;
		&:hover {
			background-color: $blue;
		}
	}
}

@media (min-width: 481px) {
	.contacts-form {
		&__link {
			font-size: 35px;
			line-height: 40px;
			&::before {
				width: 50px;
				height: 50px;
				background-size: 70%;
			}
		}
	}
}

@media (min-width: 1025px) {
	.feedback {
		margin-top: 100px;
		&__wrap {
			position: absolute;
			z-index: 5;
			width: 100%;
			left: 0;
			top: -126px;
			clip-path: polygon(10% 0%, 100% 0%, 100% 100%, 0% 100%);
			margin-left: 6%;
		}
		&__form {
			padding-top: 100px;
		}
	}
}

@media (min-width: 1281px) {
	.feedback {
		height: 800px;
		&::after {
			position: absolute;
			z-index: 1;
			content: "";
			right: 0;
			top: 0;
			width: 70%;
			height: 100%;
			clip-path: polygon(10% 0%, 100% 0%, 100% 100%, 0% 100%);
			background-image: url(../img/feedback.jpg);
			background-repeat: no-repeat;
			background-position: center;
			background-size: cover;
		}
		&__contacts {
			flex-direction: row;
			justify-content: end;
		}
		&__form {
			position: relative;
			top: 50%;
			transform: translateY(-50%);
			width: 50%;
			z-index: 3;
		}
	}
	.contacts-form__link {
		margin: auto;
		flex: 0 0 400px;
	}
	.feedback__contacts .title {
		margin-right: 50px;
		flex: 1 0 500px;
	}
}

// .reviews.reviews.reviews.reviews.reviews.reviews.reviews.reviews.reviews.reviews
.img-slider {
	&__item {
		position: relative;
		width: 100%;
		padding-bottom: 50%;
		overflow: hidden;
		& img {
			position: absolute;
			left: 0;
			right: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}

.text-slider {
	position: relative;
	margin-top: 50px;
	overflow: hidden;
	padding-bottom: 50px;
	&__wrap {
		text-align: center;
	}
	&__item {
		text-align: left;
	}

	&__reviews {
		position: relative;
		font-weight: 500;
		font-size: 15px;
		line-height: 24px;
		color: #4c4c4d;
		padding-left: 20px;
		margin-bottom: 20px;
		&::before {
			position: absolute;
			content: "";
			left: 0;
			top: 0;
			width: 3px;
			height: 100%;
			background-color: $yellow;
		}
	}
	&__person {
		display: flex;
		align-items: center;
	}
	&__img {
		position: relative;
		width: 100px;
		height: 100px;
		border-radius: 50%;
		overflow: hidden;
		& img {
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	&__client {
		margin-left: 30px;
		font-weight: 600;
		font-size: 18px;
		line-height: 24px;
		color: #000000;
		& span {
			display: block;
			font-weight: 400;
			font-size: 13px;
			line-height: 24px;
			text-transform: uppercase;
			color: #001029;
		}
	}
}
.slider-buttons {
	width: 100px;
	height: 40px;
	position: absolute;
	z-index: 5;
	left: 50%;
	bottom: 0px;
	transform: translateX(-50%);
}

.swiper-button-prev,
.swiper-button-next {
	position: absolute;
	width: 30px;
	height: 30px;
	border-radius: 3px;
	background-color: $yellow;
	&::after {
		content: "";
		width: 100%;
		height: 100%;
		background-repeat: no-repeat;
		background-position: center;
		background-size: 20px 20px;
	}
}

.swiper-button-prev {
	&::after {
		background-image: url(../img/icons/arow-l.svg);
	}
}

.swiper-button-next {
	&::after {
		background-image: url(../img/icons/arow-r.svg);
	}
}

@media (min-width: 1281px) {
	.reviews {
		background-color: #f0f1f2;
		&__wrap {
			display: flex;
			align-items: center;
			padding-right: max(15px, calc((100% - 1530px) / 2));
		}
	}

	.img-slider {
		clip-path: polygon(0% 0.2%, 100% 0%, 75% 100%, 0% 100%);
		position: relative;
		width: calc(100% - 490px - 30px);
		&__item {
			position: relative;
			width: 100%;
			height: 700px;
			padding-bottom: 0;
			overflow: hidden;
			& img {
				position: absolute;
				left: 0;
				right: 0;
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}

	.text-slider {
		clip-path: polygon(20% 0%, 100% 0%, 100% 100%, 0% 100%);
		padding: 130px;
		background-color: #f0f1f2;
		position: relative;
		margin-left: -400px;
	}
}
// .reviews.reviews.reviews.reviews.reviews.reviews.reviews.reviews.reviews.reviews
