.description,
.question {
	& h2,
	h3,
	h4,
	h5 {
		margin: 50px 0;
	}
	&__wrap {
		p {
			line-height: 28px;
			text-align: justify;
		}
	}
}

.advantag_description {
	position: relative;
	margin: 30px 0 50px;
	&::after {
		position: absolute;
		content: "";
		width: 100%;
		height: 2px;
		bottom: -10px;
		left: 0;
		background-color: #e3e4e5;
	}
}

.img-slider-article {
	position: relative;
	margin: 20px 0;
	width: 100%;
}

.img-slider-article > .slider-buttons {
	left: 60px;
}

.img-slider-article button {
	background-color: rgba(255, 192, 0, 0.43);
	&::after {
		opacity: 0.6;
	}
	&:hover {
		box-shadow: 0px 0px 10px 1px #ffc000b5;
	}
}

.advantage-list {
	margin-top: 50px;
	& li {
		position: relative;
		margin: 20px 0;
		padding-left: 25px;
		&::before {
			position: absolute;
			content: "";
			left: 0;
			top: 50%;
			transform: translateY(-50%);
			width: 14px;
			height: 14px;
			background-color: $blue;
			border-radius: 50%;
			transition: background-color 0.3s ease-in-out;
		}
		&:hover {
			&::before {
				background-color: $yellow;
			}
		}
	}
}

.feedback-article {
	margin-top: 50px;
	background-color: transparent;

	&__title {
		margin-bottom: 50px;
		text-align: center;
		font-weight: 800;
	}
	&::after {
		display: none;
	}
}
// .accordion.accordion.accordion.accordion.accordion.accordion.accordion
.question {
	margin-bottom: 100px;
	&__wrap {
		
	}
	& h5 {
		font-size: 18px;
		font-weight: 600;
	}
}
.accordion{
	padding: 10px;
	box-shadow: -10px 20px 40px rgba(0, 16, 41, 0.25);
	margin-bottom: 20px;
}
.accordion__control {
	width: 100%;
	background-color: transparent;
	cursor: pointer;
}

.accordion__content {
	max-height: 0;
	opacity: 0;
	overflow: hidden;
	will-change: max-height;
	transition: all 0.3s ease-out;
	box-sizing: content-box;
}

.accordion__title {
	font-weight: bold;
	font-size: 1.17em;
}

.accordion__icon {
	transition: transform 0.3s ease-out;
}

.open .accordion__icon {
	transform: rotate(45deg);
}

.open .accordion__content {
	opacity: 1;
	padding: 20px;
}


.accordion__control {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.accordion__control::-webkit-details-marker {
	display: none;
}

.accordion__icon {
	position: relative;
	display: inline-block;
	width: 30px;
	height: 30px;
	border-radius: 5px;
	background-color: $yellow;
}

.accordion__icon::before {
	content: "";
	position: absolute;
	left: 50%;
	top: 50%;
	width: 50%;
	height: 1px;
	background-color: #000;
	transform: translate(-50%, -50%);
}

.accordion__icon::after {
	content: "";
	position: absolute;
	left: 50%;
	top: 50%;
	width: 1px;
	height: 50%;
	background-color: #000;
	transform: translate(-50%, -50%);
}




@media (min-width: 1025px) {
	.feedback-article {
		margin-top: 50px;
		&__form {
			padding-top: 0;
		}
	}
}

@media (min-width: 1281px) {
	.feedback-article{
		height:auto;
	}
	.img-slider-article {
		width: 100%;
		clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
	}

	.feedback-article__form {
		top: 0;
		transform: translateY(0);
		width: 100%;
	}
}
