@font-face {
  font-family: "Inter";
  font-display: swap;
  src: url("../fonts/Inter-Extra.woff") format("woff"), url("../fonts/Inter-Extra.woff2") format("woff2");
  font-weight: 800;
  font-style: normal;
}

@font-face {
  font-family: "Inter";
  font-display: swap;
  src: url("../fonts/Inter-Bold.woff") format("woff"), url("../fonts/Inter-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: "Inter";
  font-display: swap;
  src: url("../fonts/Inter-SemiBold.woff") format("woff"), url("../fonts/Inter-SemiBold.woff2") format("woff2");
  font-weight: 600;
  font-style: normal;
}

@font-face {
  font-family: "Inter";
  font-display: swap;
  src: url("../fonts/Inter-Medium.woff") format("woff"), url("../fonts/Inter-Medium.woff2") format("woff2");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: "Inter";
  font-display: swap;
  src: url("../fonts/Inter-Regular.woff") format("woff"), url("../fonts/Inter-Regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "Inter";
  font-display: swap;
  src: url("../fonts/Inter-Light.woff") format("woff"), url("../fonts/Inter-Light.woff2") format("woff2");
  font-weight: 300;
  font-style: normal;
}

* {
  padding: 0px;
  margin: 0px;
  border: 0px;
  transition: all 0s ease 0s;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

aside,
nav,
footer,
header,
section {
  display: block;
}

html,
body {
  height: 100%;
  min-width: 320px;
}

body {
  line-height: 1;
  font-family: "inter";
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: "inter";
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: inherit;
  font-size: inherit;
}

body {
  color: #001029;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
}

body._lock {
  overflow: hidden;
}

.wrapper {
  position: relative;
  width: 100%;
  min-height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

._container {
  max-width: 1170px;
  padding: 0 15px;
  margin: 0 auto;
}

._container_hd {
  padding: 0 15px;
  max-width: 1920px;
  margin: 0 auto;
}

._container_single {
  padding: 0 15px;
  max-width: 800px;
  margin: 0 auto;
}

h2,
h3,
h4,
h5 {
  font-weight: 700;
  line-height: 36px;
}

h2 {
  font-size: 32px;
}

h3 {
  font-size: 28px;
}

h4,
h5 {
  font-size: 26px;
}

.title {
  display: inline-block;
  position: relative;
  padding: 20px 0;
  color: #001029;
  text-align: right;
}

.title__span {
  padding-left: 120px;
  position: absolute;
  right: 0;
  top: 0;
  font-weight: 500;
  font-size: 15px;
  line-height: 18px;
  text-transform: uppercase;
}

.title__span::before {
  position: absolute;
  content: "";
  width: 100px;
  height: 3px;
  background-color: #ffc000;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}

.title__title {
  font-size: 32px;
  font-weight: 800;
  line-height: 42px;
}

.title_wite {
  color: #fff;
}

.menu__icon {
  display: none;
}

.menu__body {
  padding: 5px 0;
}

.menu__list > li {
  position: relative;
  margin: 0px 0px 0px 20px;
}

.menu__link {
  font-weight: 500;
  font-size: 15px;
  line-height: 18px;
  color: #001029;
}

.menu__link_btn {
  background: transparent;
}

.menu__link:hover {
  color: #ffc000;
}

.menu__sub-list {
  position: absolute;
  top: 100%;
  left: 0;
  background-color: #f8f8f8;
  min-width: 350px;
}

.menu__sub-list li {
  margin: 0px 0px 10px 0px;
}

.menu__sub-list li:last-child {
  margin: 0;
}

.menu__sub-link {
  color: #001029;
  padding: 15px 40px 15px 40px;
  display: block;
}

.menu__sub-link:hover {
  background-color: #ffc000;
}

.menu__arrow {
  display: none;
}

/* ----------------------- */

body._pc .menu__list > li:hover .menu__sub-list {
  opacity: 1;
  visibility: visible;
  transform: translate(0px, 0px);
  pointer-events: all;
  z-index: 99;
}

body._touch .menu__list > li {
  display: flex;
  align-items: center;
}

body._touch .menu__link {
  flex: 1 1 auto;
}

body._touch .menu__arrow {
  display: block;
  width: 0;
  height: 0;
  margin: 0px 0px 0px 5px;
  transition: transform 0.3s ease 0s;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  border-top: 10px solid #fff;
}

body._touch .menu__list > li._active .menu__sub-list {
  opacity: 1;
  visibility: visible;
  transform: translate(0px, 0px);
  pointer-events: all;
}

body._touch .menu__list > li._active .menu__arrow {
  transform: rotate(-180deg);
}

/* ----------------------- */

.header__wrap {
  height: 72px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__logo {
  padding-left: 15px;
}

.header__menu {
  flex: 1 1 100%;
  margin: 0 60px;
  border-left: solid 2px #cccfd4;
  border-right: solid 2px #cccfd4;
}

.header__contacts {
  padding-left: 34px;
  padding-right: 15px;
  flex: 0 0 205px;
  text-align: right;
  font-weight: 600;
  font-size: 16px;
  line-height: 19px;
  color: #0439ab;
  background-image: url(../img/icons/phone.svg);
  background-repeat: no-repeat;
  background-position: left center;
}

.header__contacts span {
  margin-top: 3px;
  display: block;
  font-weight: 500;
  font-size: 13px;
  line-height: 16px;
}

.intro {
  padding: 80px 0;
  background-color: #0439ab;
}

.intro__text {
  background-color: #0439ab;
  color: #fff;
}

.intro__text span {
  position: relative;
  padding-left: 110px;
  font-size: 15px;
  font-weight: 500;
  text-transform: uppercase;
}

.intro__text span::before {
  content: "";
  position: absolute;
  z-index: 1;
  height: 3px;
  width: 100px;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  background-color: #ffc000;
  border-radius: 3px;
}

.intro__text h1 {
  margin-top: 40px;
  font-size: 43px;
  font-weight: 600;
  letter-spacing: 0.9px;
}

.intro__img {
  display: none;
}

.services__wrap {
  padding: 100px 0;
  text-align: center;
}

.services__list {
  padding: 25px 0;
  display: flex;
  flex-direction: column;
  text-align: left;
}

.service {
  border: solid 1px #e3e4e5;
  border-radius: 6px;
  margin: 20px 0;
  padding: 50px 50px 40px 50px;
  color: #001029;
  font-size: 15px;
  font-weight: 600;
  transition: all 0.3s ease-in-out;
  background-position: 1160px;
  background-repeat: no-repeat;
  background-size: cover;
}

.service__img {
  position: relative;
  margin-bottom: 25px;
  width: 60px;
  height: 60px;
}

.service__img img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.service__img::before {
  position: absolute;
  content: "";
  right: -30px;
  top: -25px;
  width: 75px;
  height: 75px;
  border-radius: 50%;
  background-color: #efefef;
}

.service__title {
  margin-bottom: 15px;
  font-size: 20px;
}

.service__price {
  color: #7e7f80;
  margin-bottom: 80px;
}

.service__link {
  position: relative;
  color: #001029;
}

.service__link:after {
  content: "";
  position: absolute;
  right: -50px;
  top: 50%;
  width: 40px;
  height: 40px;
  transform: translateY(-50%);
  background-color: #ffc000;
  border-radius: 5px;
}

.service__link::before {
  content: "";
  clip-path: polygon(40% 30%, 40% 70%, 60% 50%);
  position: absolute;
  z-index: 10;
  right: -50px;
  top: 50%;
  width: 40px;
  height: 40px;
  transform: translateY(-50%);
  background-color: #001029;
}

.service:hover {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  color: #fff;
}

.service:hover .service__price,
.service:hover .service__link {
  transition: all 0.3s ease-in-out;
  color: #fff;
}

.work {
  position: relative;
}

.work::before {
  position: absolute;
  z-index: 1;
  content: "";
  height: 150px;
  width: 100%;
  clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
  left: 0;
  top: 0;
  background-color: #e3e4e5;
}

.work__number {
  padding: 40px 0;
  z-index: 5;
  position: relative;
}

.number {
  display: flex;
  justify-content: space-between;
}

.number__item {
  flex: 1 0 140px;
  padding: 10px;
}

.number__value {
  display: inline-block;
  position: relative;
  font-weight: 700;
  font-size: 30px;
  color: #0439ab;
}

.number__value_plus::after {
  position: absolute;
  content: "";
  width: 20px;
  height: 20px;
  right: -15px;
  top: -15px;
  background-image: url(../img/icons/plus.svg);
  background-size: contain;
  background-repeat: no-repeat;
}

.number__value_precent::after {
  position: absolute;
  z-index: 10;
  content: "";
  width: 20px;
  height: 20px;
  right: -15px;
  top: -15px;
  background-image: url(../img/icons/precent.svg);
  background-size: contain;
  background-repeat: no-repeat;
}

.number__descr {
  font-weight: 600;
  font-size: 15px;
  line-height: 22px;
}

.work-stage {
  margin-top: -50px;
  padding: 100px 0;
  background: linear-gradient(rgba(4, 57, 171, 0.85)), url(../img/bg-work.png);
  background-position: center;
}

.work-stage__wrap {
  text-align: center;
}

.work-stage__list {
  margin-top: 50px;
  display: flex;
  flex-wrap: wrap;
}

.work-stage__item {
  flex: 1 1 100%;
  color: #ffffff;
  margin: 40px auto;
  padding: 20px;
  align-items: center;
  z-index: 3;
}

.work-stage__img {
  position: relative;
  margin: 0 auto;
  width: 180px;
  height: 180px;
  border-radius: 50%;
  background-color: #fff;
}

.work-stage__img img {
  position: absolute;
  width: 60%;
  height: 60%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  border-bottom: solid 3px #ffc000;
  object-fit: contain;
}

.work-stage__title {
  font-weight: 700;
  font-size: 26px;
  line-height: 31px;
  margin: 15px 0;
}

.work-stage__descr {
  font-weight: 400;
  font-size: 15px;
  line-height: 24px;
}

.advantages {
  padding: 110px 0;
}

.advantages__wrap {
  display: flex;
  flex-direction: column;
}

.advantages__descr {
  text-align: center;
}

.advantages__paragraph {
  font-size: 15px;
  line-height: 25px;
  text-align: justify;
  padding-bottom: 30px;
}

.advantages__items {
  text-align: left;
}

.advantages__img-wrap {
  position: relative;
}

.advantages__img {
  margin-top: 50px;
  position: relative;
  width: 100%;
  padding-bottom: 100%;
  overflow: hidden;
}

.advantages__img img {
  position: absolute;
  width: 100%;
  height: auto;
  top: 0;
  left: 0;
}

.advantages__number {
  color: #0439ab;
  padding: 10px;
  position: absolute;
  width: 100px;
  height: 100px;
  bottom: -30px;
  right: 0;
  background-color: #fff;
  border-left: solid 2px #ffc000;
  font-size: 30px;
  font-weight: 800;
  text-transform: uppercase;
  display: block;
}

.advantages__number span {
  margin-top: 10px;
  display: block;
  font-size: 13px;
  font-weight: 600;
  color: #001029;
  text-transform: none;
}

.advantages__number::after {
  position: absolute;
  content: "";
  width: 20px;
  height: 20px;
  right: 0px;
  top: 2px;
  background-image: url(../img/icons/plus.svg);
  background-size: contain;
  background-repeat: no-repeat;
}

.advantag {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -10px;
  margin-bottom: 20px;
}

.advantag__item {
  padding: 10px;
  display: flex;
}

.advantag__icon {
  position: relative;
  flex: 0 0 60px;
  width: 60px;
  height: 60px;
}

.advantag__icon img {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
}

.advantag__title {
  flex: 0 1 180px;
  margin-left: 8px;
  font-weight: 500;
  font-size: 16px;
  line-height: 25px;
}

.director {
  border-top: solid 1px #e3e4e5;
  padding-top: 20px;
  display: flex;
  align-items: center;
}

.director__img {
  position: relative;
  width: 90px;
  height: 90px;
  border-radius: 50%;
  overflow: hidden;
}

.director__img img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.director__data {
  margin-left: 30px;
  display: block;
  font-weight: 500;
  font-size: 18px;
  line-height: 25px;
  text-transform: uppercase;
}

.director__data span {
  display: block;
  font-size: 15px;
  line-height: 25px;
  color: #7e7f80;
  text-transform: none;
}

.feedback {
  position: relative;
  background-color: #0439ab;
}

.feedback__wrap {
  background-color: #e3e4e5;
  padding: 40px 0;
}

.feedback__contacts {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.feedback__form {
  background-color: #ffc000;
  padding: 50px 0;
}

.title__descr {
  font-weight: 500;
  font-size: 11px;
  line-height: 16px;
}

.contacts-form__link {
  position: relative;
  font-weight: 600;
  font-size: 20px;
  line-height: 25px;
  padding-left: 60px;
  margin-top: 40px;
}

.contacts-form__link::before {
  position: absolute;
  content: "";
  width: 40px;
  height: 40px;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  background-image: url(../img/icons/phone.svg);
  background-repeat: no-repeat;
  background-position: center;
  background-size: 40%;
  background-color: #fff;
  border-radius: 50%;
}

.contacts-form__mode {
  position: absolute;
  left: 62px;
  top: -15px;
  font-weight: 500;
  font-size: 13px;
  line-height: 13px;
  color: #001029;
}

.form {
  display: flex;
  flex-direction: column;
  padding: 80px 20px;
}

.form__select {
  margin: 0px auto 20px;
  padding: 16px 30px 16px 30px;
  border-radius: 3px;
  max-width: 500px;
  width: 100%;
}

.form__label {
  margin: auto;
  margin-bottom: 20px;
  max-width: 500px;
  width: 100%;
}

.form__input {
  width: 100%;
  padding: 16px 30px 16px 30px;
  border-radius: 3px;
}

.form__button {
  margin: auto;
  border-radius: 3px;
  padding: 16px 30px 16px 30px;
  max-width: 200px;
  width: 100%;
  background-color: #001029;
  color: #fff;
  transition: background-color 0.3s ease-in-out;
  font-weight: 600;
  font-size: 17px;
  line-height: 21px;
}

.form__button:hover {
  background-color: #0439ab;
}

.img-slider__item {
  position: relative;
  width: 100%;
  padding-bottom: 50%;
  overflow: hidden;
}

.img-slider__item img {
  position: absolute;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.text-slider {
  position: relative;
  margin-top: 50px;
  overflow: hidden;
  padding-bottom: 50px;
}

.text-slider__wrap {
  text-align: center;
}

.text-slider__item {
  text-align: left;
}

.text-slider__reviews {
  position: relative;
  font-weight: 500;
  font-size: 15px;
  line-height: 24px;
  color: #4c4c4d;
  padding-left: 20px;
  margin-bottom: 20px;
}

.text-slider__reviews::before {
  position: absolute;
  content: "";
  left: 0;
  top: 0;
  width: 3px;
  height: 100%;
  background-color: #ffc000;
}

.text-slider__person {
  display: flex;
  align-items: center;
}

.text-slider__img {
  position: relative;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  overflow: hidden;
}

.text-slider__img img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.text-slider__client {
  margin-left: 30px;
  font-weight: 600;
  font-size: 18px;
  line-height: 24px;
  color: #000000;
}

.text-slider__client span {
  display: block;
  font-weight: 400;
  font-size: 13px;
  line-height: 24px;
  text-transform: uppercase;
  color: #001029;
}

.slider-buttons {
  width: 100px;
  height: 40px;
  position: absolute;
  z-index: 5;
  left: 50%;
  bottom: 0px;
  transform: translateX(-50%);
}

.swiper-button-prev,
.swiper-button-next {
  position: absolute;
  width: 30px;
  height: 30px;
  border-radius: 3px;
  background-color: #ffc000;
}

.swiper-button-prev::after,
.swiper-button-next::after {
  content: "";
  width: 100%;
  height: 100%;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 20px 20px;
}

.swiper-button-prev::after {
  background-image: url(../img/icons/arow-l.svg);
}

.swiper-button-next::after {
  background-image: url(../img/icons/arow-r.svg);
}

.footer {
  background-color: #001029;
  padding: 20px 0;
}

.footer__logo-img {
  position: relative;
  width: 165px;
  height: 74px;
}

.footer__logo-img img {
  position: absolute;
  left: 0;
  top: 0;
