  height: 7px;
  border-radius: 50%;
  background-color: #ffc000;
}

.footer__link:hover {
  color: #ffc000;
}

.footer__contacts {
  color: #fff;
}

.footer__contacts-tel {
  position: relative;
  padding-left: 35px;
  margin: 25px 0;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  display: block;
  color: #fff;
}

.footer__contacts-tel span {
  margin-top: 5px;
  padding-left: 55px;
  display: block;
  font-weight: 400;
  font-size: 13px;
  line-height: 16px;
}

.footer__contacts-tel::before {
  position: absolute;
  content: "";
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 23px;
  height: 23px;
  background-image: url(../img/icons/phone.svg);
  background-repeat: no-repeat;
  background-size: contain;
}

.footer__contacts-locatio {
  position: relative;
  padding-left: 35px;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
}

.footer__contacts-locatio::before {
  position: absolute;
  content: "";
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 23px;
  height: 23px;
  background-image: url(../img/icons/location.svg);
  background-repeat: no-repeat;
  background-size: contain;
}

.description h2,
.description h3,
.description h4,
.description h5,
.question h2,
.question h3,
.question h4,
.question h5 {
  margin: 50px 0;
}

.description__wrap p,
.question__wrap p {
  line-height: 28px;
  text-align: justify;
}

.advantag_description {
  position: relative;
  margin: 30px 0 50px;
}

.advantag_description::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 2px;
  bottom: -10px;
  left: 0;
  background-color: #e3e4e5;
}

.img-slider-article {
  position: relative;
  margin: 20px 0;
  width: 100%;
}

.img-slider-article > .slider-buttons {
  left: 60px;
}

.img-slider-article button {
  background-color: rgba(255, 192, 0, 0.43);
}

.img-slider-article button::after {
  opacity: 0.6;
}

.img-slider-article button:hover {
  box-shadow: 0px 0px 10px 1px rgba(255, 192, 0, 0.7098039216);
}

.advantage-list {
  margin-top: 50px;
}

.advantage-list li {
  position: relative;
  margin: 20px 0;
  padding-left: 25px;
}

.advantage-list li::before {
  position: absolute;
  content: "";
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 14px;
  height: 14px;
  background-color: #0439ab;
  border-radius: 50%;
  transition: background-color 0.3s ease-in-out;
}

.advantage-list li:hover::before {
  background-color: #ffc000;
}

.feedback-article {
  margin-top: 50px;
  background-color: transparent;
}

.feedback-article__title {
  margin-bottom: 50px;
  text-align: center;
  font-weight: 800;
}

.feedback-article::after {
  display: none;
}

.question {
  margin-bottom: 100px;
}

.question h5 {
  font-size: 18px;
  font-weight: 600;
}

.accordion {
  padding: 10px;
  box-shadow: -10px 20px 40px rgba(0, 16, 41, 0.25);
  margin-bottom: 20px;
}

.accordion__control {
  width: 100%;
  background-color: transparent;
  cursor: pointer;
}

.accordion__content {
  max-height: 0;
  opacity: 0;
  overflow: hidden;
  will-change: max-height;
  transition: all 0.3s ease-out;
  box-sizing: content-box;
}

.accordion__title {
  font-weight: bold;
  font-size: 1.17em;
}

.accordion__icon {
  transition: transform 0.3s ease-out;
}

.open .accordion__icon {
  transform: rotate(45deg);
}

.open .accordion__content {
  opacity: 1;
  padding: 20px;
}

.accordion__control {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.accordion__control::-webkit-details-marker {
  display: none;
}

.accordion__icon {
  position: relative;
  display: inline-block;
  width: 30px;
  height: 30px;
  border-radius: 5px;
  background-color: #ffc000;
}

.accordion__icon::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  width: 50%;
  height: 1px;
  background-color: #000;
  transform: translate(-50%, -50%);
}

.accordion__icon::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  width: 1px;
  height: 50%;
  background-color: #000;
  transform: translate(-50%, -50%);
}

@media (min-width: 481px) {
  ._container_hd {
    padding: 0;
  }

  .intro {
    background-color: transparent;
    padding: 0;
  }

  .intro__wrapp {
    position: relative;
  }

  .intro__text {
    height: 80%;
    max-width: 450px;
    position: absolute;
    z-index: 5;
    top: 50%;
    transform: translateY(-50%);
    padding: 80px 80px 80px 20px;
    clip-path: polygon(0% 0%, 99.86% 0%, 80.11% 100%, 0% 100%);
  }

  .intro__text h1 {
    max-width: 440px;
  }

  .intro__img {
    margin-left: auto;
    display: block;
    position: relative;
    height: 500px;
    width: 80%;
    clip-path: polygon(17% 0%, 100% 0%, 100% 100%, 0% 100%);
    overflow: hidden;
  }

  .intro__img img {
    position: absolute;
    right: 0;
    left: 0;
    width: 150%;
    height: 100%;
    object-fit: cover;
  }

  .contacts-form__link {
    font-size: 35px;
    line-height: 40px;
  }

  .contacts-form__link::before {
    width: 50px;
    height: 50px;
    background-size: 70%;
  }
}

@media (min-width: 641px) {
  .title__span {
    font-size: 15px;
  }

  .title__title {
    font-size: 48px;
    line-height: 50px;
  }

  .services__list {
    justify-content: center;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0 -10px;
  }

  .service {
    flex: 1 1 360px;
    margin: 10px;
  }

  .work::before {
    clip-path: polygon(0% 0%, 88% 0%, 82% 100%, 0% 100%);
  }

  .number {
    justify-content: start;
  }

  .number__item {
    flex: 0 1 180px;
    padding: 10px;
  }

  .work-stage__item {
    flex: 0 0 50%;
  }
}

@media (min-width: 768px) {
  .menu__list {
    display: flex;
    align-items: center;
  }

  .menu__list > li {
    padding: 10px 0;
  }

  .menu__sub-list {
    transform: translate(0px, 10%);
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition: all 0.3s ease 0s;
  }
}

@media (min-width: 961px) {
  .intro__text {
    max-width: 600px;
    width: 100%;
  }

  .intro__img {
    height: 600px;
  }

  .work::before {
    height: 180px;
  }

  .number {
    justify-content: start;
  }

  .number__item {
    flex: 0 1 250px;
    padding: 10px;
  }

  .number__value {
    font-size: 60px;
  }
}

@media (min-width: 1025px) {
  .work-stage {
    position: relative;
  }

  .work-stage::before {
    content: "";
    position: absolute;
    z-index: 2;
    left: 0;
    bottom: 320px;
    width: 100%;
    height: 200px;
    background-image: url(../img/line.svg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
  }

  .work-stage__item {
    flex: 0 0 25%;
  }

  .work-stage__item_move {
    padding-top: 50px;
  }

  .advantages__wrap {
    flex-direction: row;
  }

  .advantages__descr {
    padding-right: 40px;
  }

  .advantages__img-wrap {
    align-items: stretch;
    flex: 0 0 35%;
  }

  .advantages__img {
    height: 100%;
  }

  .advantag {
    flex-wrap: nowrap;
  }

  .feedback {
    margin-top: 100px;
  }

  .feedback__wrap {
    position: absolute;
    z-index: 5;
    width: 100%;
    left: 0;
    top: -126px;
    clip-path: polygon(10% 0%, 100% 0%, 100% 100%, 0% 100%);
    margin-left: 6%;
  }

  .feedback__form {
    padding-top: 100px;
  }

  .footer__wrap {
    display: flex;
    justify-content: space-between;
  }

  .footer__logo {
    flex: 0 0 168px;
  }

  .footer__contacts {
    margin-top: 30px;
  }

  .feedback-article {
    margin-top: 50px;
  }

  .feedback-article__form {
    padding-top: 0;
  }
}

@media (min-width: 1281px) {
  .intro__text {
    max-width: 900px;
    width: 100%;
  }

  .service {
    flex: 0 1 360px;
  }

  .feedback {
    height: 800px;
  }

  .feedback::after {
    position: absolute;
    z-index: 1;
    content: "";
    right: 0;
    top: 0;
    width: 70%;
    height: 100%;
    clip-path: polygon(10% 0%, 100% 0%, 100% 100%, 0% 100%);
    background-image: url(../img/feedback.jpg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }

  .feedback__contacts {
    flex-direction: row;
    justify-content: end;
  }

  .feedback__form {
    position: relative;
    top: 50%;
    transform: translateY(-50%);
    width: 50%;
    z-index: 3;
  }

  .contacts-form__link {
    margin: auto;
    flex: 0 0 400px;
  }

  .feedback__contacts .title {
    margin-right: 50px;
    flex: 1 0 500px;
  }

  .reviews {
    background-color: #f0f1f2;
  }

  .reviews__wrap {
    display: flex;
    align-items: center;
    padding-right: max(15px, (100% - 1530px) / 2);
  }

  .img-slider {
    clip-path: polygon(0% 0.2%, 100% 0%, 75% 100%, 0% 100%);
    position: relative;
    width: calc(100% - 490px - 30px);
  }

  .img-slider__item {
    position: relative;
    width: 100%;
    height: 700px;
    padding-bottom: 0;
    overflow: hidden;
  }

  .img-slider__item img {
    position: absolute;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  .text-slider {
    clip-path: polygon(20% 0%, 100% 0%, 100% 100%, 0% 100%);
    padding: 130px;
    background-color: #f0f1f2;
    position: relative;
    margin-left: -400px;
  }

  .feedback-article {
    height: auto;
  }

  .img-slider-article {
    width: 100%;
    clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
  }

  .feedback-article__form {
    top: 0;
    transform: translateY(0);
    width: 100%;
  }
}

@media (max-width: 768px) {
  .menu {
    padding-right: 15px;
  }

  .menu__link {
    color: #fff;
  }

  .menu__icon {
    z-index: 99;
    display: block;
    position: relative;
    width: 30px;
    height: 18px;
    cursor: pointer;
  }

  .menu__icon span,
  .menu__icon::before,
  .menu__icon::after {
    left: 0;
    position: absolute;
    height: 14%;
    width: 100%;
    transition: all 0.3s ease 0s;
    background-color: #0439ab;
  }

  .menu__icon::before,
  .menu__icon::after {
    content: "";
  }

  .menu__icon::before {
    top: 0;
  }

  .menu__icon::after {
    bottom: 0;
  }

  .menu__icon span {
    top: 50%;
    transform: scale(1) translate(0px, -50%);
  }

  .menu__icon._active span {
    transform: scale(0) translate(0px, -50%);
  }

  .menu__icon._active::before {
    top: 50%;
    transform: rotate(-45deg) translate(0px, -50%);
    background-color: #ffc000;
  }

  .menu__icon._active::after {
    bottom: 50%;
    transform: rotate(45deg) translate(0px, 50%);
    background-color: #ffc000;
  }

  .menu__body {
    position: fixed;
    z-index: 98;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 16, 41, 0.97);
    padding: 100px 30px 30px 30px;
    transition: left 0.3s ease 0s;
    overflow: auto;
  }

  .menu__body._active {
    left: 0;
  }

  .menu__body::before {
    content: "";
    width: 100%;
    top: 0;
    left: 0;
    height: 70px;
    z-index: 2;
  }

  .menu__list > li {
    flex-wrap: wrap;
    margin: 0px 0px 30px 0px;
  }

  .menu__list > li:last-child {
    margin-bottom: 0;
  }

  .menu__list > li._active .menu__sub-list {
    display: block;
  }

  .menu__link {
    font-size: 24px;
    text-align: left;
    letter-spacing: 2px;
  }

  .menu__sub-list {
    position: relative;
    background-color: rgba(0, 16, 41, 0.97);
    flex: 1 1 100%;
    margin: 20px 0px 0px 0px;
    display: none;
  }

  .menu__sub-link {
    font-size: 20px;
    color: #fff;
  }

  .header__menu {
    border-left: none;
    border-right: none;
    flex: 0 0 30px;
    order: 3;
    margin: 0;
  }

  .header__contacts {
    padding: 10px 0 10px 34px;
  }

  .header__contacts span {
    display: none;
  }
}

@media (max-width: 480px) {
  .header__contacts {
    display: none;
  }
}