.menu__icon {
	display: none;
}
.menu__body {
	padding: 5px 0;
}
.menu__list > li {
	position: relative;
	margin: 0px 0px 0px 20px;
}
.menu__link {
	font-weight: 500;
	font-size: 15px;
	line-height: 18px;
	color: $dark-text;
}
.menu__link_btn {
	background: transparent;
}
.menu__link:hover {
	color: $yellow;
}
.menu__sub-list {
	position: absolute;
	top: 100%;
	left: 0;
	background-color: #f8f8f8;
	min-width: 350px;
}
.menu__sub-list li {
	margin: 0px 0px 10px 0px;
}
.menu__sub-list li:last-child {
	margin: 0;
}
.menu__sub-link {
	color: $dark-text;
	padding: 15px 40px 15px 40px;
	display: block;
}
.menu__sub-link:hover {
	background-color: $yellow;
}
.menu__arrow {
	display: none;
}
/* ----------------------- */
body._pc .menu__list > li:hover .menu__sub-list {
	opacity: 1;
	visibility: visible;
	transform: translate(0px, 0px);
	pointer-events: all;
	z-index: 99;
}
body._touch .menu__list > li {
	display: flex;
	align-items: center;
}
body._touch .menu__link {
	flex: 1 1 auto;
}
body._touch .menu__arrow {
	display: block;
	width: 0;
	height: 0;
	margin: 0px 0px 0px 5px;
	transition: transform 0.3s ease 0s;
	border-left: 5px solid transparent;
	border-right: 5px solid transparent;
	border-top: 10px solid #fff;
}

body._touch .menu__list > li._active .menu__sub-list {
	opacity: 1;
	visibility: visible;
	transform: translate(0px, 0px);
	pointer-events: all;
}
body._touch .menu__list > li._active .menu__arrow {
	transform: rotate(-180deg);
}

/* ----------------------- */
@media (min-width: 768px) {
	.menu__list {
		display: flex;
		align-items: center;
	}
	.menu__list > li {
		padding: 10px 0;
	}
	.menu__sub-list {
		transform: translate(0px, 10%);
		opacity: 0;
		visibility: hidden;
		pointer-events: none;
		transition: all 0.3s ease 0s;
	}
}
@media (max-width: 768px) {
	.menu {
		padding-right: 15px;
	}
	.menu__link {
		color: #fff;
	}
	.menu__icon {
		z-index: 99;
		display: block;
		position: relative;
		width: 30px;
		height: 18px;
		cursor: pointer;
	}
	.menu__icon span,
	.menu__icon::before,
	.menu__icon::after {
		left: 0;
		position: absolute;
		height: 14%;
		width: 100%;
		transition: all 0.3s ease 0s;
		background-color: $blue;
	}
	.menu__icon::before,
	.menu__icon::after {
		content: "";
	}
	.menu__icon::before {
		top: 0;
	}
	.menu__icon::after {
		bottom: 0;
	}
	.menu__icon span {
		top: 50%;
		transform: scale(1) translate(0px, -50%);
	}

	.menu__icon._active span {
		transform: scale(0) translate(0px, -50%);
	}
	.menu__icon._active::before {
		top: 50%;
		transform: rotate(-45deg) translate(0px, -50%);
		background-color: $yellow;
	}
	.menu__icon._active::after {
		bottom: 50%;
		transform: rotate(45deg) translate(0px, 50%);
		background-color: $yellow;
	}

	.menu__body {
		position: fixed;
		z-index: 98;
		top: 0;
		left: -100%;
		width: 100%;
		height: 100%;
		background-color: rgba(0, 16, 41, 0.97);
		padding: 100px 30px 30px 30px;
		transition: left 0.3s ease 0s;
		overflow: auto;
	}
	.menu__body._active {
		left: 0;
	}

	.menu__body::before {
		content: "";
		width: 100%;
		top: 0;
		left: 0;
		height: 70px;
		z-index: 2;
	}

	.menu__list > li {
		flex-wrap: wrap;
		margin: 0px 0px 30px 0px;
	}
	.menu__list > li:last-child {
		margin-bottom: 0;
	}
	.menu__list > li._active .menu__sub-list {
		display: block;
	}
	.menu__link {
		font-size: 24px;
		text-align: left;
		letter-spacing: 2px;
	}
	.menu__sub-list {
		position: relative;

		background-color: rgba(0, 16, 41, 0.97);
		flex: 1 1 100%;
		margin: 20px 0px 0px 0px;
		display: none;
	}
	.menu__sub-link {
		font-size: 20px;
		color: #fff;
	}
}

.header {
	&__wrap {
		height: 72px;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}
	&__logo {
		padding-left: 15px;
	}
	&__menu {
		flex: 1 1 100%;
		margin: 0 60px;
		border-left: solid 2px #cccfd4;
		border-right: solid 2px #cccfd4;
	}
	&__contacts {
		padding-left: 34px;
		padding-right: 15px;
		flex: 0 0 205px;
		text-align: right;
		font-weight: 600;
		font-size: 16px;
		line-height: 19px;
		color: $blue;
		background-image: url(../img/icons/phone.svg);
		background-repeat: no-repeat;
		background-position: left center;
		& span {
			margin-top: 3px;
			display: block;
			font-weight: 500;
			font-size: 13px;
			line-height: 16px;
		}
	}
}

@media (max-width: 768px) {
	.header {
		&__menu {
			border-left: none;
			border-right: none;
			flex: 0 0 30px;
			order: 3;
			margin: 0;
		}
		&__contacts {
			padding: 10px 0 10px 34px;
			& span {
				display: none;
			}
		}
	}
}

@media (max-width: 480px) {
	.header__contacts {
		display: none;
	}
}
