.footer {
	background-color: #001029;
	padding: 20px 0;
	&__logo-img {
		position: relative;
		width: 165px;
		height: 74px;
		& img {
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}
	&__descr {
		max-width: 480px;
		margin-top: 15px;
		color: #fff;
		font-weight: 300;
		font-size: 14px;
		line-height: 20px;
	}
	&__menu {
		margin: 30px 0;
		font-weight: 600;
		font-size: 18px;
		line-height: 24px;
		color: #fff;
	}
	&__item {
		padding: 5px;
	}
	&__link {
		position: relative;
		padding-left: 20px;
		font-weight: 300;
		font-size: 15px;
		line-height: 24px;
		color: #ffffff;
		transition: all 0.3s ease-in-out;
		&::before {
			position: absolute;
			content: "";
			left: 0;
			top: 50%;
			transform: translateY(-50%);
			width: 7px;
			height: 7px;
			border-radius: 50%;
			background-color: $yellow;
		}
		&:hover {
			color: $yellow;
		}
	}
	&__contacts {
		color: #fff;
	}
	&__contacts-tel {
		position: relative;
		padding-left: 35px;
		margin: 25px 0;
		font-weight: 500;
		font-size: 20px;
		line-height: 24px;
		display: block;
		color: #fff;
		& span {
			margin-top: 5px;
			padding-left: 55px;
			display: block;
			font-weight: 400;
			font-size: 13px;
			line-height: 16px;
		}
		&::before {
			position: absolute;
			content: "";
			left: 0;
			top: 50%;
			transform: translateY(-50%);
			width: 23px;
			height: 23px;
			background-image: url(../img/icons/phone.svg);
			background-repeat: no-repeat;
			background-size: contain;
		}
	}
	&__contacts-locatio {
		position: relative;
		padding-left: 35px;
		font-weight: 500;
		font-size: 20px;
		line-height: 24px;
		&::before {
			position: absolute;
			content: "";
			left: 0;
			top: 50%;
			transform: translateY(-50%);
			width: 23px;
			height: 23px;
			background-image: url(../img/icons/location.svg);
			background-repeat: no-repeat;
			background-size: contain;
		}
	}
}

@media (min-width: 1025px) {
	.footer {
		&__wrap{
			display: flex;
			justify-content: space-between;
		}
		&__logo{
			flex: 0 0 168px;
		}
		&__contacts{
			margin-top: 30px;
		}
	}
}
